
import {useState, useEffect} from 'react'

const ProgressReplace = (value, progressBar) => {
  let start = 12.5 * (value - 1)
  let end = 12.5 * value
  console.log(start, end);
  return progressBar.replace(`white ${start}%, white ${end}%`, `black ${start}%, black ${end}%`)
}

const Border = ({width, height , progress, children}) => {
  const [progressBar, setProgressBar] = useState('radial-gradient( circle closest-side, transparent 100%, white 0 ), conic-gradient( white 0%, white 12.5%, white 12.5%, white 25%, white 25%, white 37.5%, white 37.5%, white 50%, white 50%, white 62.5%, white 62.5%, white 75%, white 75%, white 87.5%, white 87.5%, white 100% )')
  let values = [2, 4, 6, 8];
  useEffect(() => {
    
    console.log(progress);
    if (values.length !== progress.length) {
      for( const value of progress){
        if (!values.includes(value)) setProgressBar(ProgressReplace(value, progressBar));
      }
      values = progress
    }
  
    return () => {}
  }, [progress, progressBar])
  
  
  return (
    <div  
    style={{
      width: `${width + 10}px`, 
      height: `${height + 10}px`, 
      backgroundColor: 'black', 
      display: 'flex', 
      alignItems: 'center', 
      justifyContent:'center', 
      borderRadius: '50%', 
      // transform: 'rotate(-20deg)',
      backgroundImage: progressBar }}>
        {children}
    </div>
  )
}

export default Border